<style scoped>
.infos {
  width: 100%;
}
.infocenter {
  padding: 24px 40px;
}
.infocenter > p {
  padding: 12px;
  display: flex;
}
.infocenter > p > span:nth-child(1) {
  padding: 12px 24px;
  background: #f3f5f7;
  margin-right: 50px;
}
.infocenter > p > span:nth-child(2) {
  flex: 1;
  padding: 12px 24px;
  border-bottom: 1px solid #d9dde1;
}
</style>
<template>
  <div>
    <div class="infos">
      <h2>
        我的收藏
      </h2>
      <div class="infocenter">
        <p v-show="msg==''" v-for="(item, index) in datas" :key="index">
          <span>课程名字</span><span>{{ item.page_title }}</span>
        </p>
        <p>{{ msg }}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datas: [],
      msg:""
    };
  },
  methods: {
    collect() {
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/collect",
        params: {
          uid: JSON.parse(window.sessionStorage.getItem("user")).id
        }
      }).then(res => {
        if (res.data.data){
          this.datas = res.data.data;
        } else {
          this.msg ="你还没收藏任何东西";
        }
       
      });
    }
  },
  mounted() {
    this.collect();
  }
};
</script>
